import React, { useState, useEffect } from 'react';
import "./App.css";

export default function App() {

  const [categories, setCategories] = useState([]);
  const [jokes, setJokes] = useState([]);

  useEffect( () => {

const renderedVal=async()=>{
  await  fetch(`https://api.chucknorris.io/jokes/categories`)
      .then((response) => response.json())
      .then((data) => { setCategories(data) });
}


renderedVal();

  }, [categories])


  const handleChange = async (e) => {
    const caty = e.target.value;
    const url = `https://api.chucknorris.io/jokes/random?category=${caty}`;
    console.log(url);
    await fetch(url)
      .then((response) => response.json())
      .then((data) => {
        setJokes(data);
        console.log(data);

      })

  };


 
    return (

      <div className="main-container"      >

        <h1 className="headline">CHUCK NORRIS  JOKES</h1>
        <select onChange={handleChange}>
          <option>Select By category</option>
          {categories?.map((category) => (
            <option key={category}>{category}</option>
          ))}
        </select>
        <di>
          <img src={jokes?.icon_url} alt='randpics' />
          <h1 className="main-content">{jokes?.value}</h1>
        </di>
      </div>
    );
  
}


